.wrapper {
  display: flex;
  flex-direction: column;
  padding: 20px 100px;
  background: linear-gradient(rgba(0, 0, 0, 0.9), rgba(0, 0, 0, 0.9)), url(../../../assets/img/films.jpeg);
  color: #fff;
}
@media (max-width: 760px) {
  .wrapper {
    padding: 20px 50px 50px;
  }
}
@media (max-width: 480px) {
  .wrapper {
    padding: 20px 30px 30px;
  }
}
.wrapper h2 {
  display: inline-block;
  font-size: 40px;
  margin-right: 40px;
}
.searchBar {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.searchBar .searchIcon {
  display: inline-block;
  mask-image: url(../../../assets/icons/loupe.svg);
  -webkit-mask-image: url(../../../assets/icons/loupe.svg);
  mask-size: 15px 20px;
  -webkit-mask-size: 15px 20px;
  width: 15px;
  height: 20px;
  background: #f65261;
  cursor: pointer;
}
.searchBar .searchIcon:hover {
  position: absolute;
  top: 0;
  right: 0;
  filter: contrast(190%);
  mask-size: 35px 40px;
  -webkit-mask-size: 35px 40px;
  width: 35px;
  height: 40px;
}
.movieInfo {
  display: flex;
  flex-wrap: nowrap;
  margin: 30px 0;
}
.movieInfo > *:not(:last-child) {
  margin-right: 40px;
}
@media (max-width: 480px) {
  .movieInfo {
    flex-direction: column;
  }
  .movieInfo > *:not(:last-child) {
    margin-bottom: 40px;
  }
}
.poster {
  width: 40%;
  max-height: 400px;
  background-color: rgba(66, 66, 66, 0.7);
}
@media (max-width: 480px) {
  .poster {
    width: 100%;
    min-height: 400px;
  }
}
.mainInfoContainer {
  width: 60%;
}
.mainInfoContainer > *:not(:last-child) {
  margin-bottom: 20px;
}
@media (max-width: 480px) {
  .mainInfoContainer {
    width: 100%;
  }
}
.numberCharacteristics {
  color: #f65261;
  font-size: 20px;
}
.numberCharacteristics > *:not(:last-child) {
  margin-right: 40px;
}
.votes {
  display: inline-block;
  width: 60px;
  height: 60px;
  padding: 15px 0 0 0;
  font-size: 30px;
  font-family: 'MontserratBold';
  border: 1px solid #fff;
  box-sizing: border-box;
  border-radius: 50%;
  text-align: center;
  color: green;
}
