.wrapper {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  padding: 50px;
  background: rgba(66, 66, 66, 0.7);
  box-sizing: border-box;
  overflow-y: scroll;
}
@media (max-width: 760px) {
  .wrapper {
    padding: 10px;
  }
}
@media (max-width: 480px) {
  .wrapper {
    padding: 0;
  }
}
.modalWindow {
  position: relative;
  min-width: 40%;
  height: max-content;
  margin: auto;
  padding: 50px;
  box-sizing: border-box;
  background: #232323;
  color: #fff;
}
.modalWindow h2 {
  font-family: 'MontserratMedium';
  font-size: 50px;
  text-align: center;
}
@media (max-width: 760px) {
  .modalWindow h2 {
    font-size: 30px;
  }
}
@media (max-width: 480px) {
  .modalWindow h2 {
    font-size: 20px;
  }
}
.modalWindow p {
  margin: 30px 0;
  font-size: 20px;
  text-align: center;
}
.modalWindow fieldset {
  display: flex;
  flex-direction: column;
  margin: 20px 0;
}
.modalWindow label {
  margin-bottom: 10px;
  font-size: 16px;
  color: #f65261;
}
.modalWindow input,
.modalWindow select {
  width: 100%;
  box-sizing: border-box;
  padding: 10px;
  border-radius: 5px;
  font-size: 16px;
  background-color: #424242;
  color: #fff;
}
.modalWindow input:-internal-autofill-selected,
.modalWindow select:-internal-autofill-selected {
  background-color: #424242;
  color: #fff;
}
.modalWindow input:focus,
.modalWindow select:focus,
.modalWindow input:hover,
.modalWindow select:hover {
  filter: brightness(190%);
}
.modalWindow input[type="date"]::-webkit-calendar-picker-indicator {
  mask-image: url(../../../../assets/icons/calendar.svg);
  -webkit-mask-image: url(../../../../assets/icons/calendar.svg);
  mask-size: 25px 24px;
  -webkit-mask-size: 25px 24px;
  width: 20px;
  height: 20px;
  background: #f65261;
}
.modalWindow input[type="date"]::-webkit-datetime-edit {
  color: transparent;
}
.modalWindow input[type="date"]:before {
  content: attr(placeholder);
  color: grey;
}
.modalWindow input[type="date"]:focus:before {
  content: none !important;
}
.modalWindow input[type="date"]:focus::-webkit-datetime-edit {
  color: #fff !important;
}
.modalWindow button,
.modalWindow input[type='submit'] {
  width: fit-content;
  padding: 10px 30px;
  border-radius: 5px;
  border: 1px solid #f65261;
  font-size: 16px;
  background: none;
  color: #f65261;
  cursor: pointer;
}
.modalWindow button ~ button,
.modalWindow input[type='submit'] ~ button,
.modalWindow button ~ input[type='submit'],
.modalWindow input[type='submit'] ~ input[type='submit'] {
  margin-left: 20px;
}
.modalWindow button:focus,
.modalWindow input[type='submit']:focus,
.modalWindow button:hover,
.modalWindow input[type='submit']:hover {
  filter: contrast(190%);
}
@media (max-width: 480px) {
  .modalWindow button,
  .modalWindow input[type='submit'] {
    width: 100%;
    padding: 15px;
    font-size: 20px;
    font-family: 'MontserratBold';
  }
  .modalWindow button ~ button,
  .modalWindow input[type='submit'] ~ button,
  .modalWindow button ~ input[type='submit'],
  .modalWindow input[type='submit'] ~ input[type='submit'] {
    margin-left: 0;
    margin-top: 20px;
  }
}
@media (max-width: 760px) {
  .modalWindow {
    min-width: 70%;
  }
}
@media (max-width: 480px) {
  .modalWindow {
    width: 100%;
  }
}
