nav {
  display: flex;
  justify-content: space-between;
  border-bottom: 3px solid #232323;
  color: #fff;
  font-size: 20px;
  overflow-x: auto;
}
nav ul {
  display: flex;
}
nav ul li {
  padding: 20px 0;
  margin-right: 10px;
  text-transform: uppercase;
}
nav ul li:hover {
  color: #f65261;
  cursor: pointer;
}
@media (max-width: 1024px) {
  .sorting {
    display: none;
  }
}
.mobileSorting {
  display: none;
}
@media (max-width: 1024px) {
  .mobileSorting {
    display: inline-block;
  }
}
.sorting,
.mobileSorting {
  position: relative;
  padding: 12px 0;
}
.sorting::after,
.mobileSorting::after {
  position: absolute;
  top: 28px;
  right: 20px;
  content: '';
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 7px 7px 0 7px;
  border-color: #f65261 transparent transparent transparent;
}
.sorting label,
.mobileSorting label {
  margin-right: 10px;
  font-family: 'MontserratLight';
  color: #fff;
  align-self: center;
}
.sorting select,
.mobileSorting select {
  width: 200px;
  padding: 5px;
  font-size: 20px;
  background: none;
  color: #fff;
  text-transform: uppercase;
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
}
.sorting select option,
.mobileSorting select option {
  background: #555;
  font-size: 20px;
}
.postNavigation {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 760px) {
  .postNavigation {
    justify-content: flex-end;
  }
}
.counting {
  display: inline-block;
  padding: 20px 0;
  color: #fff;
  font-size: 20px;
}
@media (max-width: 760px) {
  .counting {
    display: none;
  }
}
