header {
  padding: 20px 100px 100px;
  background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url(../../../assets/img/films.jpeg);
}
@media (max-width: 760px) {
  header {
    padding: 20px 50px 50px;
  }
}
@media (max-width: 480px) {
  header {
    padding: 20px 30px 30px;
  }
}
.addButton {
  float: right;
  padding: 20px;
  font-size: 20px;
  font-family: 'MontserratBold';
  border-radius: 5px;
  background: rgba(66, 66, 66, 0.7);
  color: #f65261;
}
@media (max-width: 760px) {
  .addButton {
    padding: 10px;
    font-size: 16px;
  }
}
.addButton:focus,
.addButton:hover {
  filter: brightness(190%);
}
.searchContainer {
  padding: 50px;
}
@media (max-width: 1024px) {
  .searchContainer {
    padding: 50px 0;
  }
}
.searchContainer label {
  font-size: 50px;
  font-family: 'MontserratLight';
  color: #fff;
}
.search {
  margin-top: 50px;
}
.search input,
.search button {
  box-sizing: border-box;
  padding: 20px;
  font-size: 20px;
  border-radius: 5px;
  color: #fff;
}
.search input {
  width: 78%;
  margin-right: 2%;
  background: linear-gradient(rgba(66, 66, 66, 0.7), #424242, rgba(66, 66, 66, 0.7));
}
@media (max-width: 760px) {
  .search input {
    width: 100%;
    margin-right: 0;
    margin-bottom: 10px;
  }
}
.search input::placeholder {
  color: #232323;
  font-family: 'MontserratMedium';
}
.search input:focus,
.search input:hover {
  filter: brightness(190%);
}
.search button {
  width: 20%;
  background: #f65261;
  font-family: 'MontserratMedium';
}
@media (max-width: 1024px) {
  .search button {
    padding: 20px 10px;
  }
}
@media (max-width: 760px) {
  .search button {
    width: 50%;
    margin: 0 25%;
  }
}
.search button:focus,
.search button:hover {
  filter: contrast(190%);
}
