/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
hgroup,
menu,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
button,
input,
select {
  border: none;
}
button:focus,
input:focus,
select:focus {
  outline: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
@font-face {
  font-family: MontserratRegular;
  src: url(../../../assets/fonts/Montserrat-Regular.ttf);
}
@font-face {
  font-family: MontserratMedium;
  src: url(../../../assets/fonts/Montserrat-Medium.ttf);
}
@font-face {
  font-family: MontserratLight;
  src: url(../../../assets/fonts/Montserrat-Light.ttf);
}
@font-face {
  font-family: MontserratBold;
  src: url(../../../assets/fonts/Montserrat-Bold.ttf);
}
:global(.react-select__control) {
  background-color: #424242 !important;
  border: none !important;
  font-family: 'MontserratRegular' !important;
}
:global(.react-select__control--is-focused) {
  box-shadow: none !important;
}
:global(.react-select__indicator-separator) {
  width: 0 !important;
}
:global(.react-select__menu) {
  font-family: 'MontserratRegular' !important;
  background-color: #424242 !important;
}
:global(.react-select__multi-value) {
  background: #f65261 !important;
}
:global(.react-select__multi-value__label) {
  color: #fff !important;
}
:global(.react-select__option--is-focused) {
  background: #f65261 !important;
}
html {
  height: 100%;
}
body {
  height: 100%;
  font-family: 'MontserratRegular';
  background: #555;
}
input,
button,
select {
  font-family: 'MontserratRegular';
}
[id='root'] {
  display: flex;
  flex-direction: column;
  height: 100%;
}
button[data-ui='primary'],
input[type='submit'][data-ui='primary'] {
  background: #f65261;
  color: #fff;
}
main {
  flex: auto;
  margin-top: 10px;
  padding: 0 100px 40px;
  background: #424242;
  color: #fff;
}
@media (max-width: 760px) {
  main {
    padding: 0 50px 30px;
  }
}
@media (max-width: 480px) {
  main {
    padding: 0 30px 30px;
  }
}
.loader {
  margin: auto;
  border: 10px solid #555;
  border-top: 10px solid #3498db;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  animation: spin 2s linear infinite;
}
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.moviesContainer {
  display: flex;
  flex-wrap: wrap;
  margin: -20px -2.5%;
}
@media (max-width: 480px) {
  .moviesContainer {
    margin: -20px 0;
  }
}
.active {
  border-bottom: 3px solid #f65261;
}
