.card {
  position: relative;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  width: 33.33%;
  padding: 20px 2.5%;
  color: #fff;
}
@media (max-width: 760px) {
  .card {
    width: 50%;
  }
}
@media (max-width: 480px) {
  .card {
    width: 100%;
    padding: 20px 0;
  }
}
.card:hover {
  cursor: pointer;
  filter: brightness(70%);
}
.card:hover .menuIcon {
  display: block;
}
.card:hover .menuIcon:hover {
  filter: brightness(170%);
}
.card .menuIcon {
  display: none;
  position: absolute;
  top: 5%;
  right: 11%;
  padding: 5px 20px;
  font-size: 40px;
  background: #424242;
  border-radius: 50%;
  box-shadow: 0 0 10px #555;
}
.card .menu {
  display: none;
  position: absolute;
  top: 5%;
  right: 11%;
  width: 40%;
  margin: 10px;
  padding: 30px 0 10px;
  background: #424242;
  border-radius: 5px;
}
.card .menu .option {
  padding: 10px 30px;
}
.card .menu .option:hover {
  background: #f65261;
  cursor: pointer;
}
.card .menu .closeButton {
  position: absolute;
  top: 0;
  right: 5px;
  background: none;
  cursor: pointer;
}
.card .menu .closeButton:hover .closeIcon {
  color: #f65261;
}
.card .menu .closeIcon {
  display: block;
  font-size: 30px;
  color: #fff;
  transform: rotate(45deg);
}
@media (max-width: 760px) {
  .card .menu {
    width: 70%;
  }
  .card .menu .option {
    padding: 10px 20px;
  }
}
@media (max-width: 480px) {
  .card .menu .option {
    padding: 20px;
    font-size: 20px;
  }
}
.card img {
  flex: auto;
  width: 100%;
  min-height: 200px;
  margin-bottom: 20px;
  background-color: #232323;
}
.card .mainInfoContainer {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}
.card h4 {
  font-size: 20px;
}
.card .date {
  padding: 5px 10px;
  border: 1px solid #fff;
  border-radius: 3px;
}
