.buttons {
  display: flex;
  justify-content: flex-end;
  margin-top: 50px;
}
@media (max-width: 480px) {
  .buttons {
    flex-direction: column;
  }
}
input[type='date'].full:before {
  content: none !important;
}
input[type='date'].full::-webkit-datetime-edit {
  color: #fff !important;
}
.selectIcon {
  margin: 10px;
  border-style: solid;
  border-width: 7px 7px 0 7px;
  border-color: #f65261 transparent transparent transparent;
}
.notValid {
  border: 1px solid #f65261;
  border-radius: 5px;
}
.error {
  margin-top: 5px;
  font-size: 12px;
  color: #f65261;
}
input[type='submit']:disabled {
  background: #555 !important;
  border-color: #424242 !important;
}
